.slider{
  width: auto;
  height :280px;
  overflow: hidden;

  @media @desktop-widths {
    height: 744px;
  }
}

.slider-items{
  width:400%;
  height:100%;
  margin: 0;
  padding: 0;
  -webkit-animation:slide 16s infinite;
  -moz-animation:slide 16s infinite;
  animation:slide 16s infinite;

  @media @desktop-widths {
    height: 100%;
  }
}

.slider__item-1,
.slider__item-2,
.slider__item-3,
.slider__item-4{
  width:25%;
  height:100%;
  float:left;
}

.slider__image {
  object-fit: cover;
    height: 300px;
    width: 3500px;

  @media @desktop-widths {
    object-fit: cover;
      height: 800px;
      width: 3500px;
  }
}

