@-webkit-keyframes slide {
    0%,100% { margin-left: 0%; }
    12% { margin-left: 0%; }
    24% { margin-left: -100%; }
    36% { margin-left: -100%; }
    48% { margin-left: -200%; }
    60% { margin-left: -200%; }
    72% { margin-left: -300%; }
    84% { margin-left: -300%; }
}

@-moz-keyframes slide {
  0%,100% { margin-left: 0%; }
    12% { margin-left: 0%; }
    24% { margin-left: -100%; }
    36% { margin-left: -100%; }
    48% { margin-left: -200%; }
    60% { margin-left: -200%; }
    72% { margin-left: -300%; }
    84% { margin-left: -300%; }
}

@keyframes slide {
  0%,100% { margin-left: 0%; }
    12% { margin-left:0%; }
    24% { margin-left: -100%; }
    36% { margin-left: -100%; }
    48% { margin-left: -200%; }
    60% { margin-left: -200%; }
    72% { margin-left: -300%; }
    84% { margin-left: -300%; }
}

@keyframes slide12345 {
  0%,100% { margin-left: 0%; }
    12% { margin-left:0%; }
