    36% { margin-left: -100%; }
    48% { margin-left: -200%; }
    60% { margin-left: -200%; }
    72% { margin-left: -300%; }
    84% { margin-left: -300%; }
}

.slider-bullets {
  position: absolute;
  top: 260px;
  left: 50%;
  width: 200px;
  z-index: 6;
  font-size: 0;
  text-align: center;
  margin: 0 -100px;


  @media @desktop-widths {
    top: 700px;
    left: 50%;
  }
}

.slider-bullet-1,
.slider-bullet-2,
.slider-bullet-3,
.slider-bullet-4 {
  margin: 0 10px;
  padding: 10px;
  border-width: 6px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1px @color-theme;
  position: relative;
  top: 50%;
  float: left;
}

.slider-bullet-1 {
  -webkit-animation: bullet-1 16s infinite;
  -moz-animation: bullet-1 16s infinite;
  animation: bullet-1 16s infinite;
}

@-webkit-keyframes bullet-1{
  0%,100% { background: @color-theme; }
  12% { background: @color-theme; }
  24% { background: @color-transparency; }
  84% { background: @color-transparency;
    }
}

@-moz-keyframes bullet-1 {
  0%,100% { background: @color-theme; }
  12% { background: @color-theme; }
  24% { background: @color-transparency; }
  84% { background: @color-transparency; }
